
import {
  defineComponent,
  reactive,
  ref,
  shallowRef,
  onBeforeUnmount,
  onMounted,
  nextTick,
} from "vue";
import "@wangeditor/editor/dist/css/style.css"; // 引入 css
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";
import { IDomEditor } from "@wangeditor/editor";
import { ElInput } from "element-plus";
import { addDataList, addRelational, getAllStation } from "@/api/request";
import { notice } from "@/utils/common";
import type { FormInstance } from "element-plus";
import AvatarUpload from "@/components/upload/UploadAvatar.vue";
import DataBind from "@/components/admin/DataBind.vue";
import PageCopyright from "@/layout/PageCopyright.vue";
import mapBoxGl, { AnySourceData } from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import "@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css";
import MapboxDraw from "@mapbox/mapbox-gl-draw";
import { uuid } from "@/utils/common";
import { classList } from "@/common-config";
import { DataListType, StationType } from "@/type";
import { styleLight } from "@/utils/mapStyleJson";
export default defineComponent({
  components: {
    Editor,
    Toolbar,
    AvatarUpload,
    DataBind,
    PageCopyright,
  },
  setup() {
    const defaultProps = {
      children: "children",
      label: "label",
    };
    const container = ref<HTMLElement>();
    const dataBind = ref();
    const inputValue = ref("");
    const inputVisible = ref(false);
    const InputRef = ref<InstanceType<typeof ElInput>>();
    const avatarUpload = ref<{ clearAvatar: () => {} }>();
    const form: Omit<
      DataListType,
      "createTime" | "updateTime" | "download" | "watch"
    > = reactive({
      id: uuid(),
      name: "",
      location: [],
      description: "",
      tags: [],
      avatar: "",

      provider: "",
      range: "",
      detail: "",
      type: "",
      providerPhone: "",
      providerEmail: "",
      providerAddress: "",
      timeStamp: "",
    });
    const stationId = ref("");
    const stationList = ref<StationType[]>([]);

    const editorRef = shallowRef<IDomEditor>();
    const toolbarConfig = {};
    const editorConfig = {
      scroll: true,
      autoFocus: true,
    };
    const fileList = ref<string[]>([]);
    const fileRef = ref<HTMLElement | undefined>();
    const metaRef = ref<HTMLElement | undefined>();

    let map: mapBoxGl.Map;

    const handleCreated = (editor: any) => {
      editorRef.value = editor; // 记录 editor 实例，重要！
    };

    const changeData = (
      val: {
        id: string;
        name: string;
        folder: boolean;
        size: string[];
        flag: boolean;
        parentId: string;
      }[]
    ) => {
      fileList.value = [];
      val.forEach((item) => {
        fileList.value.push(item.id);
      });
    };

    const commit = async (
      formEl1: FormInstance | undefined,
      formEl2: FormInstance | undefined
    ) => {
      if (!formEl1 || !formEl2) return;
      await formEl1.validate(async (valid1, fields) => {
        await formEl2.validate(async (valid2) => {
          if (valid1 && valid2) {
            const data = await addDataList(form);
            const data1 = await addRelational({
              dataListId: form.id,
              fileIdList:
                form.type !== "实时水位" ? fileList.value : [stationId.value],
            });
            if (
              data != null &&
              data.code === 0 &&
              data1 != null &&
              data1.code === 0
            ) {
              init();
              notice("success", "成功", "新条目创建成功");
            }
          }
        });
      });
    };

    const handleClose = (tag: string) => {
      form.tags.splice(form.tags.indexOf(tag), 1);
    };

    const handleInputConfirm = () => {
      if (inputValue.value) {
        form.tags.push(inputValue.value);
      }
      inputVisible.value = false;
      inputValue.value = "";
    };

    const showInput = () => {
      inputVisible.value = true;
      nextTick(() => {
        InputRef.value!.input!.focus();
      });
    };

    const returnPicture = (val: string) => {
      form.avatar = val;
    };

    const init = () => {
      form.id = uuid();
      form.name = "";
      form.description = "";
      form.location = [] as string[];
      form.tags = [] as string[];
      form.range = "";
      form.detail = "";
      form.provider = "";
      form.type = "";
      form.providerPhone = "";
      form.providerEmail = "";
      form.providerAddress = "";
      form.timeStamp = "";
      form.avatar = "";
      avatarUpload.value?.clearAvatar();
      dataBind.value?.clearData();
    };

    const fileRules = reactive({
      name: [{ required: true, message: "条目名不得为空！", trigger: "blur" }],
      tags: [{ required: true, message: "标签不得为空！", trigger: "blur" }],
    });
    const metaRules = reactive({
      provider: [
        { required: true, message: "数据提供方不得为空！", trigger: "blur" },
      ],
      type: [
        { required: true, message: "数据类型不得为空！", trigger: "blur" },
      ],
      getOnline: [
        { required: true, message: "数据获取方式不得为空！", trigger: "blur" },
      ],
    });

    const initMap = () => {
      container.value!.style.background = styleLight.background;
      map = new mapBoxGl.Map({
        container: container.value as HTMLElement,
        // style: "mapbox://styles/johnnyt/cl9miecpn001t14rspop38nyk",
        style: styleLight.styleJson as any,
        center: [121.18, 31.723],
        zoom: 8,
        accessToken:
          "pk.eyJ1Ijoiam9obm55dCIsImEiOiJja2xxNXplNjYwNnhzMm5uYTJtdHVlbTByIn0.f1GfZbFLWjiEayI6hb_Qvg",
      });
      //自定义绘制面
      const polygonDraw = new MapboxDraw({
        controls: {
          combine_features: false,
          uncombine_features: false,
          trash: true,
          point: false,
          line_string: false,
        },
      });
      //绘制事件
      const updateArea = function (e: any) {
        if (e.type === "draw.create") {
          if (polygonDraw.getAll().features.length > 1) {
            polygonDraw.delete(polygonDraw.getAll().features[0].id as string);
          }
          form.location = [] as string[];
          (e.features[0].geometry.coordinates[0] as number[][]).forEach(
            (item) => {
              form.location.push(item[0].toString());
              form.location.push(item[1].toString());
            }
          );
        } else if (e.type === "draw.update") {
          form.location = [] as string[];
          (e.features[0].geometry.coordinates[0] as number[][]).forEach(
            (item) => {
              form.location.push(item[0].toString());
              form.location.push(item[1].toString());
            }
          );
        } else if (e.type === "draw.delete") {
          form.location = [] as string[];
        }
      };
      map.addControl(polygonDraw, "top-right");
      map.on("draw.create", updateArea);
      map.on("draw.delete", updateArea);
      map.on("draw.update", updateArea);
    };

    // 组件销毁时，也及时销毁编辑器
    onBeforeUnmount(() => {
      const editor = editorRef.value;
      if (editor == null) return;
      editor.destroy();
    });

    const initStationList = async () => {
      const res = await getAllStation();
      if (res && res.code === 0) {
        stationList.value = res.data;
      }
    };

    onMounted(() => {
      initStationList();
      initMap();
    });

    return {
      avatarUpload,
      container,
      form,
      defaultProps,
      inputValue,
      inputVisible,
      InputRef,
      classList,
      editorRef,
      toolbarConfig,
      editorConfig,
      handleCreated,
      commit,
      fileRules,
      metaRules,
      fileRef,
      metaRef,
      changeData,
      dataBind,
      stationId,
      stationList,
      handleClose,
      handleInputConfirm,
      showInput,
      returnPicture,
    };
  },
});
